import React from 'react'
import Layout from "../components/layout"
import Banner from '../components/banner'
import SEO from "../components/seo"
import { Link } from 'gatsby'
import BootstrapTable from 'react-bootstrap-table-next'
import ToolkitProvider, { Search } from 'react-bootstrap-table2-toolkit'
import paginationFactory from 'react-bootstrap-table2-paginator'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs'
import { useTranslation } from 'react-i18next'
import Select from 'react-select';
import "react-tabs/style/react-tabs.css";
const { SearchBar } = Search

// Basic language component
export default ({ pageContext: { language, bibles, resources } }) => {
  const { t } = useTranslation()

  function bibleLinkFormatter(cell, row) {
    return (<Link to={"/bibles/" + row.id }>{ cell }</Link>)
  }

  const resourceLink = selectedOption => {
    window.location = selectedOption.value
  }

  function LinkListFormatter(cell, row) {
    if(typeof cell === 'object') {
      let optionList = []
      cell.forEach(function(element) {
        optionList.push({label: element.title, value: element.url})
      });

      return <Select options={optionList} onChange={resourceLink} />
    }
    return <a href={cell.url}>{cell.title}</a>
  }

  return (
  <Layout>
    <Banner
        title={ language.name }
        subtitle={ language.iso }
        image="https://images.bible.cloud/fab/banners/languages_list.jpg"
        breadcrumbs={[
          {path: '/', title: 'home'},
          {path: '/languages/', title: t('languages.title')},
          {path: '#', title: language.name }
        ]}
    />
    <SEO title={ language.name } />

    <div className="row">

    <div className="medium-12 columns">
      <Tabs defaultIndex={bibles.length !== 0 ? 0 : 1}>
        <div className="tab-list">
          <TabList className="tab-box">
            <Tab
                disabled={bibles.length === 0}>
                { t('bibles.title') + ' (' + bibles.length + ')' }
            </Tab>
            <Tab
                disabled={resources.length === 0}>
                { t('resources.title') + ' (' + resources.length + ')' }
            </Tab>
          </TabList>
        </div>
        <TabPanel>
          {(bibles.length !== 0) ? <ToolkitProvider
              keyField="id"
              data={ bibles }
              bordered={ false }
              columns={ [
                { dataField: 'id', text: 'ID', hidden: true, keyField: true },
                { dataField: 'name', text: 'English Title', sort: true, formatter: bibleLinkFormatter },
                { dataField: 'vname', text: 'Vernacular Title', sort: true, formatter: bibleLinkFormatter },
                { dataField: 'date', text: 'Date', sort: true, align: 'center', classes: 'col-small-hide',headerClasses: 'col-small-hide' },
              ] }
              search>
            {
              props => (
                  <div>
                    <SearchBar { ...props.searchProps } delay={ 100 } placeholder={"Search " + bibles.length + " Bibles"} />
                    <BootstrapTable
                        pagination={ paginationFactory() }
                        { ...props.baseProps }
                    />
                  </div>
              )
            }
          </ToolkitProvider> : 'No Bibles have been found in this language' }

        </TabPanel>
        <TabPanel>
          <h2>{ t('resources') }</h2>
          {(resources.length !== 0) ? <ToolkitProvider
              keyField="id"
              data={ resources }
              bordered={ false }
              columns={ [
                { dataField: 'id', text: 'ID', hidden: true, keyField: true },
                { dataField: 'type', text: 'Type'},
                { dataField: 'title', text: 'name'},
                //{ dataField: 'organization', text: 'organization'},
                { dataField: 'links', text: 'Links', formatter: LinkListFormatter}
              ] }
              search>
            {
              props => (
                  <div>
                    <SearchBar { ...props.searchProps } delay={ 100 } placeholder={"Search " + resources.length + " Resources"} />
                    <BootstrapTable
                        pagination={ paginationFactory() }
                        { ...props.baseProps }
                    />
                  </div>
              )
            }
          </ToolkitProvider> : 'No Bibles have been found in this language' }
        </TabPanel>
      </Tabs>
    </div>
    </div>
  </Layout>)
}
